<script>
import upload from '../assets/js/upload'
  export default {
    data() {
      return {
        right: 'right',
        fileList: [],
        fileType: '.doc,.docx,.ppt,.pptx,.xls,.xlsx,.txt,.rtf,.ppt,.bmp,.png,.jpg,.jpeg,.zip,.prt,.stp,.dxf,.dwg,.sch,.pcb,.dsn,.brd',
        form: {
          "bp_url": "",
          "business_mode": "",
          "ceo": "",
          "city": "",
          "cmo": "",
          "comment": "",
          "company_name": "",
          "contact": "",
          "contact_phone": "",
          "contact_title": "",
          "core_resource": "",
          "core_tech": "",
          "cto": "",
          "customers_resource": "",
          "description": "",
          "dest_customers": "",
          "employees": "",
          "financing_plan": "",
          "financing_sum": "",
          "full_time": "",
          "future_aim": "",
          "future_plan": "",
          "income_status": "",
          "industry": "",
          "industry_resource": "",
          "investors": "",
          "main_income": "",
          "market_capacity": "",
          "market_proficiency": "",
          "market_rate": "",
          "needs_desc": "",
          "needs_support": "",
          "organization": "",
          "our_resource": "",
          "pain_point": "",
          "potential_income": "",
          "product_status": "",
          "project_name": "",
          "recent_plan": "",
          "risk": "",
          "risk_detail": "",
          "rival": "",
          "score_evaluation": "",
          "score_industry": "",
          "score_mode": "",
          "score_needs": "",
          "score_product": "",
          "score_resource": "",
          "score_risk": "",
          "score_team": "",
          "source": "",
          "start_from": "",
          "stock_structure": "",
          "tags": "",
          "team_desc": "",
          "tech_evaluation": "",
          "timestamp": "",
          "valuation": ""
        },
        fileData: {},
        id: '',
        region_name: '',
        regions: [],
        citys: [],
        city_id: ''
      }
    },
    created() {
      this.id = this.$route.query
      this.details()
    },
    methods: {
      //  
      details() {
        var _this = this
        if (typeof this.id === 'object') {
          this.$router.push('/admin/bp_list')
        } else {
          new Promise((resolve, reject) => {
            $.ajax({
              url: '/admin/api/v1/bps/' + this.id,
              success: function(result) {
                var data = result.result
                _this.form.start_from = data.gmt_create.split('T')[0]
                Object.assign(_this.form, data)
                return resolve(data.city)
              }
            })
          }).then((city) => {
            $.ajax({
              url: '/main/api/v1/region_detail/' + city + '?page=1',
              success: function(result) {
                var data = result.result
                //  
                _this.region_name = data[0].name
                //  
                _this.region()
                _this.city(data[0].area_id)
                _this.form.city = data[1].area_id
                _this.city_id = data[1].name
              }
            })
          })
        }
      },
      isChange() {
      },
      //  
      uploadFile(ele) {
        var _this = this
        upload(ele.target, 2, () => {
          _this.form.bp_url = $("#hiddens").val()
        })
      },
      //  BP
      submitBP() {
        var _this = this
        if (this.form.start_from === '') {
          this.form.start_from = ''
        } else {
          this.form.start_from = Date.parse(new Date(this.form.start_from))
        }
        if (this.form.bp_url === "0") {
          this.$message.error('url')
        } else {
          $.ajax({
            url: '/admin/api/v1/bps/' + this.id,
            type: 'post',
            contentType: 'application/json',
            data: JSON.stringify(this.form),
            success: function(result) {
              _this.$message({
                message: result.message,
                type: 'success'
              })
              _this.$router.push('/admin/bp_list')
            }
          })
        }
      },
      //  
      region() {
        var _this = this
        $.ajax({
          url: '/main/api/v1/region?page=1',
          success: function(result) {
            var data = result.result
            _this.regions = data
          }
        })
      },
      //  
      city(id) {
        var _this = this
        if (/^[\u4e00-\u9fa5]+$/.test(id)) {
          return false
        } else {
          $.ajax({
            url: '/main/api/v1/region/' + id +'?page=1',
            success: function(result) {
              var data = result.result
              _this.citys = data
            }
          })
        }
      },
    }
  }
</script>

<style lang="scss">
.bp_manage {
  .xs {
