    height:50px;
    background:#fff;
    position: fixed;
    top:-50px;
    left:50%;
    margin-left:-250px;
    z-index:4;
    border-radius:6px;
    border:1px solid #efefef;
    line-height:50px;
    padding-left:15px;
  }
}
</style>



