import Vue from 'vue'
import Router from 'vue-router'
import signin from '@/components/signin/signin.vue'
import Home from '@/components/home.vue'
import admin_list from '@/pages/admin_list.vue'
import admin_new from '@/pages/admin_new.vue'
import project_list from '@/pages/project_list.vue'
import project_details from '@/pages/project_details.vue'
import stage_manage from '@/pages/stage_manage.vue'
import attachments from '@/pages/attachments.vue'
// import comment_list from '@/pages/comment_list.vue'
import service_category from '@/pages/service_category.vue'
import service_item from '@/pages/service_item.vue'
import service_custom from '@/pages/service_custom.vue'
import deliverable_list from '@/pages/deliverable_list.vue'
import indent_list from '@/pages/indent_list.vue'
import managers from '@/pages/managers.vue'
import department from '@/pages/department.vue'
import role from '@/pages/role.vue'
import permissions from '@/pages/permissions.vue'
import bill_list from '@/pages/bill_list.vue'
import settings from '@/pages/settings.vue'
import bp_list from '@/pages/bp_list.vue'
import bp_manage from '@/pages/bp_manage.vue'
import bp_details from '@/pages/bp_details.vue'
import NotFound from '@/pages/notFound.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/admin/signin',
      hidden: true
    },
    {
      path: '/admin',
      name: '',
      redirect: '/admin/signin',
      hidden: true
    },
    {
      path: '/admin/signin',
      name: '',
      component: signin,
      hidden: true
    },
    {
      path: '/admin/404',
      name: '',
      component: NotFound,
      hidden: true
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-user',
      component: Home,
      children: [
        {path: '/admin/admin_list', component: admin_list, iconCls: 'icon-page1', name: ''},
        {path: '/admin/admin_new', component: admin_new, iconCls: 'icon-page2', name: ''},
        // {path: '/page3', component: Page3, iconCls: 'icon-page3', name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-project',
      component: Home,
      children: [
        {path: '/admin/project_list', component: project_list, iconCls: 'icon-page4', name: ''},
        {path: '/admin/project_details', component: project_details, name: '', hidden: true},
        {path: '/admin/stage_manage', component: stage_manage, iconCls: 'icon-page5', name: ''},
        {path: '/admin/attachments', component: attachments, iconCls: 'icon-page6', name: ''},
        // {path: '/comment_list', component: comment_list, name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-server',
      component: Home,
      children: [
        {path: '/admin/service_category', component: service_category, iconCls: 'icon-page7', name: ''},
        {path: '/admin/service_item', component: service_item, iconCls: 'icon-page8', name: ''},
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-custom',
      component: Home,
      children: [
        {path: '/admin/service_custom', component: service_custom, iconCls: 'icon-page9', name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-examine',
      component: Home,
      children: [
        {path: '/admin/deliverable_list', component: deliverable_list, iconCls: 'icon-page10', name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-serverList',
      component: Home,
      children: [
        {path: '/admin/indent_list', component: indent_list, iconCls: 'icon-page11', name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-serverList',
      component: Home,
      children: [
        {path: '/admin/bill_list', component: bill_list, iconCls: 'icon-page11', name: ''}
      ]
    },
    {
      path: '/admin/home',
      name: '',
      iconCls: 'icon-system',
      component: Home,
      children: [
        {path: '/admin/managers', component: managers, iconCls: 'icon-page12', name: ''},
        {path: '/admin/department', component: department, iconCls: 'icon-page13', name: ''},
        {path: '/admin/role', component: role, iconCls: 'icon-page11', name: ''},
        {path: '/admin/permissions', component: permissions, iconCls: 'icon-page7', name: ''},
        {path: '/admin/settings', component: settings, iconCls: 'icon-page8', name: ''}
      ]
    },
     {
      path: '/admin/home',
      name: 'BP',
      iconCls: 'icon-BP',
      component: Home,
      children: [
        {path: '/admin/bp_list', component: bp_list, iconCls: 'icon-page14', name: 'BP'},
        {path: '/admin/bp_manage', name: 'BP', component: bp_manage, hidden: true},
        {path: '/admin/bp_details', name: 'BP', component: bp_details, hidden: true},
      ]
    },
    {
      path: '*',
      redirect: {path: '/admin/404'},
      hidden: true
    },
  ]
})



