<template>
   <div class="service_custom">
     <el-table
     :data="tableData"
     v-loading="loading"
     border
     element-loading-text=""
     style="width: 100%">
       <el-table-column
         align="center"
         prop="id"
         label=""
         width="80"
         show-overflow-tooltip>
       </el-table-column>
       <el-table-column
         align="center"
         prop="title"
         label=""
         width="150"
         show-overflow-tooltip>
       </el-table-column>
       <el-table-column
         align="center"
         prop="description"
         label=""
         width="400"
         show-overflow-tooltip>
       </el-table-column>
       <el-table-column
         align="center"
         prop="category_name"
         label=""
         width="150"
         show-overflow-tooltip>
       </el-table-column>
       <el-table-column
         align="center"
         prop="price"
         label="()"
         width="150"
         show-overflow-tooltip>
       </el-table-column>
       <el-table-column
         align="center"
         width="80"
         label="">
         <template scope="scope">
           <a href="scope.row.file_name"></a>
         </template>
       </el-table-column>
       <el-table-column
         align="center"
         fixed="right"
         width="80"
         label="">
         <template scope="scope">
           <el-button @click="midClick(scope.row.id)" type="text" size="small"></el-button>
         </template>
       </el-table-column>
     </el-table>
     <transition name="fade">
       <div class="modal" v-if="addShow">
         <div class="modal-dialog">
           <div class="modal-header">
             <span></span>
           </div>
           <div class="modal-content">
             <label for="">()</label>
             <el-input type="text" v-model="details.price"></el-input>
             <label for=""></label>
             <el-select placeholder="" v-model="details.status">
               <el-option
               v-for="item in conditions"
               :label="item.label"
               :value="item.value"
               :key="item.value">
               </el-option>
             </el-select>
           </div>
           <div class="modal-footer">
             <el-button type="primary" @click="ensure"></el-button>
             <el-button type="primary" @click="cancel"></el-button>
           </div>
         </div>
       </div>
     </transition>
     <v-pages :total="total" v-on:currentChange="query"></v-pages>
   </div>
</template>

<script>
import pages from '../components/pages/pages.vue'
const URL = 'https://apl-static.oss-cn-beijing.aliyuncs.com/'
export default {
  data () {
    return {
      conditions: [
        {
          value: 'Paid',
          label: ''
        },
        {
          value: 'Canceled',
          label: ''
        },
        {
          value: 'Submitting',
          label: ''
        },
        {
          value: 'Submitted',
          label: ''
        },
        {
          value: 'Confirmed',
          label: ''
        },
        {
          value: 'Finished',
          label: ''
        }
      ],
      tableData: [],
      total: 1,
      id: '',
      page: '',
      loading: false,
      addShow: false,
      details: {
        status: '',
        price: ''
      }
    }
  },
  created() {
    this.query(1)
  },
  methods: {
    reset() {
      for(var name in this.$data.form) {
        this.$data.form[name] = ''
      }
    },
    addOpen() {
      this.reset()
      this.addShow = true
    },
    cancel() {
      this.addShow = false
    },
    query(page) {
      var _this = this
      this.page = page
      $.ajax({
        url: '/admin/api/v1/custom_service_items?page=' + page,
        beforeSend: function() {
          _this.loading = true
        },
        timeout: 5000,
        success: function(result) {
          let data = result.result
          _this.loading = false
          _this.total = data.total
          for(var i in data.items) {
            data.items[i].file_name = data.items[i].file_name === null ? '#' : URL + data.items[i].file_name
          }
          _this.tableData = data.items
        },
        complete: function(XMLHttpRequest, status){ //
    if(status == 'timeout'){ //,statussuccess,error
              _this.loading = false
      _this.$message.error('')
    }
     }
      })
    },
    ensure() {
      var _this = this
      $.ajax({
        url: '/admin/api/v1/custom_service_items/' + this.id,
        type: 'post',
        contentType: 'application/json',
        data: JSON.stringify(this.details),
        success: function(result) {
          _this.addShow = false
          _this.$message({
            message: result.message,
            type: 'success'
          })
          _this.query(_this.page)
        }
      })
    },
    //  id
    midClick(id) {
      var _this = this
      this.addShow = true
      this.id = id
      $.ajax({
        url: '/admin/api/v1/custom_service_items/' + id,
        success: function(result) {
          var data = result.result
          _this.details = data
        }
      })
    },
  },
  components: {
    'v-pages': pages
  }
}
</script>

<style lang="scss">
.service_custom {
}
</style>



